header{
	box-shadow: 0 0 7px rgba(0,0,0,0.8);
	.navbar{
		background: #fff;
	}
	#nav-top{
		background-color: #36612D;
		.navbar-btn{
			color: #fff !important;
			margin: 0;
			&:hover,&:active{
				outline: 0;
				background-color: #61AD50;
				text-decoration: none;
			}
		}
	} 

	#nav-main{
		.navbar-brand{
			padding-bottom: 0;
			vertical-align: middle;
			position: relative;
			padding-top: 0;
			img{
				height: inherit;
				position: relative;
				display: inline;
				padding: 10px;
				float: left;
			}
			label{
				vertical-align: middle;
				top: 10px;
				position: relative;
				color: #36612D;
			}
			small{
				white-space: nowrap;
				font-size: 11px;
				display: block;
				color: #36612D;
			}
		}
	}
}

body{
	height: 100%;
	font-family: "Roboto",'Open Sans', sans-serif;
	background-color : #fff;
	background-size: cover;
	background-position: center;
	background-repeat: no-repeat;
	background-image: url(/images/home-interior-blur.jpg);
	background-attachment: fixed;
	&.lowerbg{
		background-image: url(/images/dormroom.jpg);
	}
}

section{
	&#main-banner{
		#lead-shout{
			margin-bottom: 50px;
			margin-top: 50px;
			color: #333;
			#logo-hero{
				height: 150px;
				width: 150px;
				background-size: contain;
				background-repeat: no-repeat;
				margin: 0 auto;
			}
			h1{
				color: #fff;
				font-weight: bold;
				text-shadow: 1px 1px 5px rgba(0,0,0,0.5);
			}
			h4{
				color: #fff;
				text-shadow: 1px 1px 5px rgba(0,0,0,0.5);
			}
		}
		#quick-search{ 
			font-size: 10px;
			box-shadow: 0 0 7px rgba(0,0,0,0.5);
		}
		min-height: 100%;
	}

	&#front-search{
		background-color: #f4f4f4;
		padding-bottom: 100px;
		h3{
			text-align: center;
			padding-top: 30px;
			padding-bottom: 30px;
		}
		#mason-gallery{
			ul{
				list-style: none;
				li.item-box{
					width: 50%;
					position: relative;
					float: left;
					background: #fff;
					&:hover{
						box-shadow: 0px 0px 10px rgba(0,0,0,0.5);
						cursor: pointer;
					}
					&:not(:first-child){
						.img-box{
							background-size: cover;
							width: 50%;
							height: 100%;
							display: inline-block;
						}
						.info-box{
							position: relative;
							vertical-align: top;
							padding: 25px;
							width: 50%;
							display: inline-block;
							float: right;
						}
						&:nth-child(odd){
							.info-box{
								float: left;
							}
						}
						&:nth-child(n+5){
							&:nth-child(odd){
								.info-box{
									float: right;
								}
							}
							&:nth-child(even){
								.info-box{
									float: left;
								}
							}
						}
						&:nth-child(n+7){
							&:nth-child(odd){
								.info-box{
									float: left;
								}
							}
							&:nth-child(even){
								.info-box{
									float: right;
								}
							}
						}
					}
					&:first-child{
						.img-box{
							width: 100%;
							height: 100%;
						}
						.info-box{
							position: absolute;
							bottom: 0;
							width: 100%;
							padding: 25px;
							color: #fff;
							background-color: rgba(0,0,0,0.5);
							label{
								font-size: 24px;
							}
						}
					}
				}
			}
		}
	}


	&#affiliates{
		background: #fff;
		.container{
			padding: 20px 15px;
			h3{
				text-align: center;
			}
			#carousel-partners{
				.partner-item{
					width: 200px;
					img{
						cursor: pointer;
						-webkit-filter: grayscale(100%);
						filter: grayscale(100%);
						filter: gray;
						filter: url("data:image/svg+xml;utf8,<svg version='1.1' xmlns='http://www.w3.org/2000/svg' height='0'><filter id='greyscale'><feColorMatrix type='matrix' values='0.3333 0.3333 0.3333 0 0 0.3333 0.3333 0.3333 0 0 0.3333 0.3333 0.3333 0 0 0 0 0 1 0' /></filter></svg>#greyscale");
						&:hover{
							-webkit-filter: none;
							filter: none;
						}
					}
				}
			}
		}
	}
	&#why-choose-us{
		color: #fff;
		background-color: #171717;
		background-size: cover;
		background-position: center bottom;
		background-repeat: no-repeat;
		background-image: url(/images/cityscape.jpg);
	}

	&#featured{
		padding: 20px 0;
		height: 555px;
		.container-fluid{
			padding: 0;
		}
		//            background: #fff;
		//            border: 1px solid #ddd;
		border-top: 0;
		h3{
			text-align: center;
			padding-top: 30px;
			padding-bottom: 30px;
			color: #fff;
		}
		#carousel-featured{
			a{
				text-decoration: none;
				color: initial;
			}
			.listing-item{
				padding: 0px;
				transition: all 200ms ease-in;
				width: 250px;
				margin-top: 0px;
				background: #fff;
				//                    border: 0.1em solid #ddd;
				border-radius: 5px;
				overflow: hidden;


				border: 0;
				box-shadow: 0 0 10px rgba(0,0,0,0.5);

				.image{
					background: #ddd;
					height: 180px;
					background-size: 101%;
					background-position: center;
					transition: background-size 0.2s ease-in;
					-moz-transition: background-size 0.2s ease-in;
					-ms-transition: background-size 0.2s ease-in;
					-o-transition: background-size 0.2s ease-in;
					-webkit-transition: background-size 0.2s ease-in;
					&:hover{
						background-size: 120%;
					}
					.project-type{
						background-color: rgba(0,0,0,0.5);
						color: #fff;
						padding: 6px 12px;
						display: block;
						text-align: center;
					}
				}
				h4.head{
					padding: 0 10px;
					text-align: center;
				}
				small{
					padding: 0 10px;
					text-align: center;
					display: block;
				}
				p.content{
					padding: 10px;
					font-size: 13px;
					transition: all 200ms ease-in;
					overflow: hidden;
				}
				ul.item-specs{
					-moz-column-count: 2;
					-moz-column-gap: 2.5em;
					-webkit-column-count: 2;
					-webkit-column-gap: 2.5em;
					column-count: 2;
					column-gap: 2.5em;
					margin-left: 3em;
				}
				p.foot{
					padding: 10px;
					font-size: 11px;
				}
			}
			.owl-item:not(.center){
				.listing-item{
					margin-top: 35px;
				}
				p.content{
					height: 0;
					padding: 0;
				}
			}
			.owl-item{
				&.center{
					.listing-item{
						border: 0;
					}
				}
			}
			.owl-stage{
				padding-bottom: 5px;
			}
		}
	}

	&#testimonials{

		background-color: #FFFFFF;
		padding-top: 50px;
		.nav.nav-justified > li > a { position: relative; }
		.nav.nav-justified > li > a:hover,
		.nav.nav-justified > li > a:focus { background-color: transparent; }
		.nav.nav-justified > li > a > .quote {
			position: absolute;
			left: 0px;
			top: 0;
			opacity: 0;
			width: 30px;
			height: 30px;
			padding: 5px;
			background-color: #61AD50;
			border-radius: 15px;
			color: #fff;  
		}
		.nav.nav-justified > li.active > a > .quote { opacity: 1; }
		.nav.nav-justified > li > a > img { box-shadow: 0 0 0 5px #61AD50; }
		.nav.nav-justified > li > a > img { 
			max-width: 100%; 
			opacity: .3; 
			-webkit-transform: scale(.8,.8);
			transform: scale(.8,.8);
			-webkit-transition: all 0.3s 0s cubic-bezier(0.175, 0.885, 0.32, 1.275);
			transition: all 0.3s 0s cubic-bezier(0.175, 0.885, 0.32, 1.275);
		}
		.nav.nav-justified > li.active > a > img,
		.nav.nav-justified > li:hover > a > img,
		.nav.nav-justified > li:focus > a > img { 
			opacity: 1; 
			-webkit-transform: none;
			transform: none;
			-webkit-transition: all 0.3s 0s cubic-bezier(0.175, 0.885, 0.32, 1.275);
			transition: all 0.3s 0s cubic-bezier(0.175, 0.885, 0.32, 1.275);
		}
		.tab-pane .tab-inner { padding: 30px 0 20px; }

		@media (min-width: 768px) {
			.nav.nav-justified > li > a > .quote {
				left: auto;
				top: auto;
				right: 20px;
				bottom: 0px;
			}  
		}
	}
}


